export default {
	props: {
		sortTabList: {
			type: Array,
			default() {
				return []
			}
		},
		top: { //距离顶部位置
			type:Number,
			default() {
				return 0
			}
		}
	},
	data() {
		return {
			activeTab: -1,
			activeSubTab: this.sortTabList.map(_=>0), //默认选中第一个
			activeTabVal: '', // 传给后端的值
			showSubList: [],
			show: false
		}
	},
	methods: {
		calculate(num) {
			return uni.upx2px(num) + 'px'
		},
		handleShowSub(i, val) {
			this.showSubList = this.sortTabList[i].subTab;
			this.show = true;
			this.activeTab = i;
			this.activeTabVal = val;
		},
		handleChooseVal(i, val) {
			this.activeSubTab.splice(this.activeTab, 1, i)
			this.$emit('chooseItem', [this.activeTabVal, val])
			this.hide()
		},
		hide() {
			this.show = false;
			this.activeTab = -1;
		}
	}
}
